body {
  margin: 0;
  padding: 0;
  height:100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.wrapper {
  height:100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.header {
  text-align: center;
  background: #BC7474;
  color: white;
  font-size: 30px;

}

.content {
  -webkit-flex: 1 1;
          flex: 1 1;
  overflow-y: scroll;
  background: #28262C;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

li {
  float: left;
}

li a {
  display: block;
  color: #F98989;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover {
  background-color: #9E5656;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.footer {
    position:relative;
    bottom:0;
    height:30px;   /* Height of the footer */
    background-color: #374A67;
    padding: 20px;
    text-align: center;
}

.footer > * {
    margin: 0px 5px;
}
.Home {
    padding-top: 10px;
    text-align: center;
}
.About {
    color: white;
    text-align:justify;
}

.projects {
    color: #2196F3;
}

.content {
    height:100vh;
    overflow: auto; 
    padding: 40px;
}
