.wrapper {
  height:100vh;
  display: flex;
  flex-direction: column;
}

.header {
  text-align: center;
  background: #BC7474;
  color: white;
  font-size: 30px;

}

.content {
  flex: 1;
  overflow-y: scroll;
  background: #28262C;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

li {
  float: left;
}

li a {
  display: block;
  color: #F98989;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover {
  background-color: #9E5656;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
